html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Quicksand', 'Baloo Bhaijaan';
}
/*   #7164cf,
    #8a58cf,
    #7a41c9,
    #6b30bc, */
body {
  background: linear-gradient(270deg, #306fbc, #2261af);
  background-size: 1400% 1400%;
  -webkit-animation: Gradient 29s ease infinite;
  -moz-animation: Gradient 29s ease infinite;
  animation: Gradient 29s ease infinite;
}
a {
  text-decoration: none;
  color: inherit;
}
@-webkit-keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@-moz-keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.head {
  color: white;
}
.container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  position: relative;
  padding-bottom: 20px;
}
.flex_c {
  width: 50%;
  display: flex;
  flex-direction: column;
  position: relative;
}
#submit {
  font-family: 'Quicksand', 'Baloo Bhaijaan';
  margin: 0.3em 0;
  border: none;
  background: rgba(51, 44, 99, 0.5);
  display: block;
  width: 100%;
  padding: 0.5em 1em;
  color: white;
  font: bold 15px;
  border-radius: 3px;
  cursor: pointer;
}
.headIcon {
  margin: 0 0.7em;
}
input[type='text'] {
  background: rgba(255, 255, 255, 0.5);
  border: none;
  font-family: 'Quicksand', 'Baloo Bhaijaan';
  padding: 1em;
  border-radius: 3px;
  outline: none;
}
::placeholder {
  color: #332c63;
}
.trashIcon,
.doneIcon,
.importantIcon,
.notImportantIcon,
.editIcon,
.saveIcon,
.cancelIcon {
  cursor: pointer;
  padding: 0.3em;
}
.editIcon,
.saveIcon,
.cancelIcon {
  color: rgba(0, 0, 0, 0.4);
}
.editIcon:hover,
.saveIcon:hover,
.cancelIcon:hover {
  transition: 0.2s;
  transform: scale(1.1);
}
.trashIcon {
  color: rgba(0, 0, 0, 0.4);
}
.doneIcon {
  color: rgba(255, 255, 255, 0.3);
}
.trashIcon:hover {
  color: rgba(0, 0, 0, 0.6);
  transition: 0.2s;
  transform: scale(1.1);
}
.doneIcon:hover {
  color: rgba(255, 255, 255, 0.6);
  transform: scale(1.1);
}
.importantIcon {
  color: rgba(255, 255, 255, 0.7);
}
.notImportantIcon {
  color: rgba(255, 255, 255, 0.4);
}
.notImportantIcon:hover {
  color: rgba(255, 255, 255, 0.7);
  transform: scale(1.1);
}

.tasks {
  background: rgba(255, 255, 255, 0.3);
  padding: 0.5em;
  padding-left: 1rem;
  border-radius: 3px;
  color: white;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding-right: 0;
  position: relative;
  /* cursor: move; */
}
.options {
  position: absolute;
  right: 0.5em;
}

.checkIcon,
.uncheckIcon {
  margin: 0 0.4em;
}

#rmvbtn {
  background: rgba(255, 255, 255, 0.09);
  text-align: center;
  padding: 0.3em;
  border-radius: 3px;
  color: rgba(255, 255, 255, 0.4);
  cursor: pointer;
  border: none;
}
#rmvbtn:hover {
  color: white;
  transition: 0.4s;
}
#rmvbtn i {
  margin-left: 1em;
}

#emptyInput {
  display: none;
}

#result {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.order {
  margin-left: 5px;
  margin-right: 8px;
}
input[type='text'].editing-active {
  background-color: transparent;
  border-bottom: 1px solid rgba(51, 44, 99, 0.5);
  padding: 0.5em;
}
.edit {
  display: none;
}

.info-block {
  color: #fff;
  opacity: 0.8;
  margin-bottom: 1rem;
  text-align: center;
}
.header {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;

  gap: 1rem;
  width: 100%;
  padding: 2rem 0;
  > ul {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    color: #fff;
    margin-left: -12px;
    > li {
      cursor: pointer;
    }
  }
}

a.active {
  border-bottom: 1px solid #fff;
}
.fa-3x {
  font-size: inherit !important;
}

.todo-text,
.async .todo-text {
  max-width: 95%;
  display: inline-block;
  white-space: break-spaces;
  overflow: hidden;
}
.todo-text {
  max-width: 88%;
}
.pagination {
  padding: 10px;
  margin: 15px 0;
  display: flex;
  justify-content: center;
}

.pagination > span {
  padding: 15px 20px;
  border: 1px solid gray;
  cursor: pointer;
}

.pagination__disable {
  opacity: 0;
}

.pagination__selected {
  background-color: rgb(220, 220, 220);
}
/* -----------------------------------------------------------
	pagination
----------------------------------------------------------- */
.pagination > li > button,
.pagination > li > span {
  position: relative;
  float: left;
  padding: 6px 12px;
  margin-left: -1px;
  line-height: 1.42857143;
  color: #337ab7;
  text-decoration: none;
  background-color: #fff;
  border: 1px solid #ddd;
  outline: 0;
  cursor: pointer;
}
.pagination > li > button:hover,
.pagination > li > span:hover,
.pagination > li > button:focus,
.pagination > li > span:focus {
  z-index: 2;
  color: #23527c;
  background-color: #eee;
  border-color: #ddd;
}
.pagination > li > button.active,
.pagination > li > span.active,
.pagination > li > button.active:hover,
.pagination > li > span.active:hover,
.pagination > li > button.active:focus,
.pagination > li > span.active:focus {
  z-index: 3;
  color: #fff;
  cursor: default;
  background-color: #337ab7;
  border-color: #337ab7;
}
.pagination > li > span:disabled,
.pagination > li > span:disabled:hover,
.pagination > li > span:disabled:focus,
.pagination > li > button:disabled,
.pagination > li > button:disabled:hover,
.pagination > li > button:disabled:focus {
  color: #777;
  cursor: not-allowed;
  background-color: #fff;
  border-color: #ddd;
}
.box_pagination_info {
  font-size: 12px;
  color: #666;
  padding-top: 9px;
}
.box_pagination .pagination {
  margin-top: 10px;
}
.box_change_pagelimit {
  text-align: right;
}
.box_change_pagelimit .form-control {
  display: inline-block;
  vertical-align: middle;
  width: 70px;
  margin: 0 5px;
}
